/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"about-tab-content\">\n  <div class=\"container\">\n    <div class=\"row\">\n      <div class=\"col-md-8\">\n        <div class=\"about-description\">\n          <p>\n            The Gokhale Institute of Public Affairs (GIPA) is an independent, non-party and non communal\n            organization endeavouring to serve as a center for the education of the public for democratic\n            citizenship. It seeks to Co-operate with, and seeks Co-operation from the Government and all public\n            institutions in the country.\n          </p>\n          <p>\n            Public Affairs, Monthly Journal of the Gokhale Institute of Public Affairs was started in the year\n            1949\n            with the objective of promoting education for and in the conscientious practice of democratic\n            citizenship.\n          </p>\n          <p>\n            A renewed and final effort - now for the fourth time - at founding the Gokhale Institute was made\n            towards the end of 1944 and letters of appeal were once again addressed. The response was\n            encouraging. A\n            meeting was called on 18th February 1945 at D.V.Gundappa's residence in Nagasandra Road. The meeting\n            adopted the draft rules and appointed a Council of Management, Congratulatory messages had been\n            received\n            from Gopalaswamy Ayyangar, Sir Mirza M. Ismail and others. The Institute in the present form was\n            thus\n            born on 18th February 1945. However, as the needed building was not available, it was only on 2nd\n            July\n            1946 that the first lecture was arranged after securing a building. That meeting was addressed by\n            Hrudayanath Kunzru, then president of the Servants of India Society.\n          </p>\n          <p>\n            The Study Circle was started on 15th December 1947 and the first book taken up for study was John\n            Stuart\n            Mill's On Liberty, Since then many Classics in English, Samskrita and Kannada have been studied. On\n            15th\n            August 1948 B.P.Wadia, Director of the Indian Institute of World Culture, Bangalore, declared the\n            Library of the G.I.P.A. open for the public use. The library now has a rare collection of valuable\n            books, and is constantly used by the general public as well as by research scholars.\n          </p>\n        </div>\n        <div class=\"about-copyright\">\n          <h1>Copyrights</h1>\n          <p>\n            The Gokhale Institute of Public Affairs (GIPA) is an independent, non-party and non communal\n            organization endeavouring to serve as a center for the education of the public for democratic\n            citizenship. It seeks to Co-operate with, and seeks Co-operation from the Government and all public\n            institutions in the country.\n          </p>\n        </div>\n\n        <div class=\"about-gallery\">\n          <h1>Gallery</h1>\n\n          <!-- <div id=\"aniimated-thumbnials\">\n          <a href=\"assets/img/organisation/gipa.png\">\n            <img src=\"assets/img/organisation/gipa.png\" />\n          </a>\n          <a href=\"assets/img/organisation/gipa.png\">\n            <img src=\"assets/img/organisation/gipa.png\" />\n          </a>\n        </div> -->\n\n          <div class=\"demo-gallery\">\n            <ul id=\"lightgallery\">\n              <li data-responsive=\"assets/img/organisation/gallery-1.jpg\"\n                data-src=\"assets/img/organisation/gallery-1.jpg\"\n                data-sub-html=\"<h4>Fading Light</h4><p>Classic view from Rigwood Jetty on Coniston Water an old archive shot similar to an old post but a little later on.</p>\">\n                <a href=\"\">\n                  <img class=\"img-responsive\" src=\"assets/img/organisation/gallery-1.jpg\">\n                  <div class=\"demo-gallery-poster\">\n                    <img src=\"assets/img/icons/zoom.png\">\n                  </div>\n                </a>\n              </li>\n              <li data-responsive=\"assets/img/organisation/gallery-2.jpg\"\n                data-src=\"assets/img/organisation/gallery-2.jpg\"\n                data-sub-html=\"<h4>Bowness Bay</h4><p>A beautiful Sunrise this morning taken En-route to Keswick not one as planned but I'm extremely happy I was passing the right place at the right time....</p>\">\n                <a href=\"\">\n                  <img class=\"img-responsive\" src=\"assets/img/organisation/gallery-2.jpg\">\n                  <div class=\"demo-gallery-poster\">\n                    <img src=\"assets/img/icons/zoom.png\">\n                  </div>\n                </a>\n              </li>\n              <li data-responsive=\"assets/img/organisation/gallery-3.jpg\"\n                data-src=\"assets/img/organisation/gallery-3.jpg\"\n                data-sub-html=\"<h4>Sunset Serenity</h4><p>A gorgeous Sunset tonight captured at Coniston Water....</p>\">\n                <a href=\"\">\n                  <img class=\"img-responsive\" src=\"assets/img/organisation/gallery-3.jpg\">\n                  <div class=\"demo-gallery-poster\">\n                    <img src=\"assets/img/icons/zoom.png\">\n                  </div>\n                </a>\n              </li>\n            </ul>\n          </div>\n        </div>\n\n      </div>\n      <div class=\"col-md-4\">\n        <div class=\"about-featured\">\n          <h1>Featured</h1>\n          <div class=\"about-featured-video\">\n            <div id=\"video-gallery\">\n              <a href=\"https://vimeo.com/1084537\" data-poster=\"assets/img/organisation/gipa.png\">\n                <img src=\"assets/img/organisation/gipa.png\" />\n                <button mat-icon-button>\n                  <img src=\"assets/img/icons/play.png\" alt=\"Play video\" />\n                </button>\n              </a>\n            </div>\n          </div>\n        </div>\n        <div class=\"about-connect\">\n          <h1>Connect</h1>\n          <ul class=\"contact-info\">\n            <li class=\"phone\">\n              <span>+91 94492 12222</span>\n            </li>\n            <li class=\"mail\">\n              <span>nitesh@subramaniamentertainment.com</span>\n            </li>\n            <li class=\"address\">\n              <span>#95, \"Shaale\" 1st Floor, 41st Cross, 3rd Main Rd, 8th Block, Jayanagar, Bengaluru, Karnataka\n                560082</span>\n            </li>\n          </ul>\n          <div class=\"social-links\">\n            <button mat-icon-button>\n              <img src=\"assets/img/icons/facebook.png\" alt=\"facebook\" />\n            </button>\n            <button mat-icon-button>\n              <img src=\"assets/img/icons/wikipedia.png\" alt=\"wikipedia\" />\n            </button>\n            <button mat-icon-button>\n              <img src=\"assets/img/icons/instagram.png\" alt=\"instagram\" />\n            </button>\n            <button mat-icon-button>\n              <img src=\"assets/img/icons/linkedin.png\" alt=\"linkedin\" />\n            </button>\n            <button mat-icon-button>\n              <img src=\"assets/img/icons/youtube.png\" alt=\"youtube\" />\n            </button>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</div>");

